.info-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 2vw;
  margin: 10px;
}

.info-box {
  border-radius: 10px;
  height: 25vh;
  background-color: #e0e0e0;
  overflow: hidden;
  /* border: 1px solid black */
}

#first-info-box {
  display: flex;
  align-items: center;
  background: linear-gradient(45deg, #ff8080, #80ff80);
}

.music-title {
  font-size: 24px;
  font-weight: bold;
  color: #333;
  margin: 0 20px;
}

.music-note-icon {
  font-size: 48px;
  color: black;
  margin: 0 10px;
  animation: moveIcon 1.5s infinite alternate;
}

@keyframes moveIcon  {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-5px);
  }
}



/*Media Queries*/
/*For Small Screens and down*/
@media (max-width: 767.98px) {
  .info-container {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2vw;
    row-gap: 2vh;
  }

}

/* Medium screens (768px to 991.98px) */
@media (min-width: 768px) and (max-width: 991.98px) {}

/* Extra-large screens (1500px and above) */
@media (min-width: 1600px) {}












